* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
}
ul,
ol,
li {
	list-style: none;
}
a {
	text-decoration: none;
}
body {
	background: #efefef;
}
fieldset {
	border: none;
}
.h {
	position: absolute;
	top: -99999px;
}
table {
	border-collapse: collapse;
}
textarea {
	resize: none;
}
